/* styles.css */

/* General Styles */
body {
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 0;
}

/* Navbar */
.navbar {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: #333;
  padding: 10px 1px;
  color: white;
  position: fixed; /* Fix the navbar */
  top: 0; /* Position it at the top */
  left: 0; /* Align it to the left */
  width: 100%; /* Make it span the full width */
  z-index: 1000; /* Ensure it stays above other content */
}

.nav-links {
  list-style: none;
  display: flex;
  gap: 20px;
}

.nav-links li {
  position: relative;
}

.nav-links a {
  color: white;
  text-decoration: none;
  padding: 10px 15px;
}

/* Dropdown Menu */
.dropdown-content {
  display: none;
  position: absolute;
  top: 100%;
  left: 0;
  background-color: rgba(255, 255, 255, 0.2);
  /* Transparent white */
  -webkit-backdrop-filter: blur(5px);
  backdrop-filter: blur(5px);

  list-style: none;
  padding: 10px 0;
  width: 165px;
  border-radius: 5px;
  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.2);
}

/* Show dropdown on hover */
.dropdown:hover .dropdown-content {
  display: block;
}

/* Make dropdown clickable */
.dropdown-content li {
  padding: 5px;
}

.dropdown-content li a {
  color: black;
}

.dropdown-content li:hover {
  background-color: #ddd;
}

.nav-buttons {
  display: flex;
  align-items: center;
  gap: 10px;
}

.nav-btn,
.signup-btn {
  padding: 8px 12px;
  border: none;
  cursor: pointer;
  border-radius: 5px;
  font-size: 14px;
  transition: background 0.3s ease-in-out;
}

.nav-btn {
  background: rgb(51, 0, 255);
}

.nav-btn:hover {
  background: rgb(30, 0, 180);
}

.signup-btn {
  background: #ff6600;
}

.signup-btn:hover {
  background: #cc5200;
}

/* Add the rest of the CSS here... */
.location-container {
  display: flex;
  align-items: center;
  gap: 5px;
}

#location-input {
  padding: 8px;
  border-radius: 5px;
  border: 1px solid #cccccc;
  font-size: 14px;
  width: 250px;
  margin-left: 300px;
}


.hero {
  position: absolute;
  left: 150px;
  width: 250px;
}

/* Modals */
.modal {
  display: none;
  /* Hidden initially */
  position: fixed;
  z-index: 1000;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  /* Semi-transparent black */
  display: flex;
  justify-content: center;
  align-items: center;
}

.modal-content {
  background: white;
  padding: 20px;
  border-radius: 10px;
  text-align: center;
  width: 300px;
  position: relative;
  /* Important for .close positioning */
}

.close {
  position: absolute;
  top: 10px;
  right: 15px;
  font-size: 25px;
  font-weight: bold;
  color: #333;
  cursor: pointer;
}

.close:hover {
  color: red;
  /* Highlight when hovered */
}



.modal-content input {
  width: 90%;
  margin: 10px 0;
  padding: 8px;
}

.modal-content button {
  padding: 10px;
  background: #ff6600;
  color: white;
  border: none;
  cursor: pointer;
}

.close {
  position: absolute;
  top: 10px;
  right: 15px;
  font-size: 20px;
  cursor: pointer;
}

.search-section {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  height: 500px;
  background: url('/public/worker-background.png') no-repeat center top;
  background-size: cover;
  color: white;
  text-shadow: 2px 2px 8px rgba(0, 0, 0, 0.6);
  padding: 30px;
  gap: 15px;
}
@keyframes textLapse {
  0% { opacity: 0; transform: translateY(20px); }
  100% { opacity: 1; transform: translateY(0); }
}

.findname {
  font-size: 36px;
  font-weight: bold;
  color: white;
  text-transform: uppercase;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 5px;
}

.findname span {
  display: inline-block;
  opacity: 0;
  animation: textLapse 0.5s forwards;
}

.findname span:nth-child(1) { animation-delay: 0s; }
.findname span:nth-child(2) { animation-delay: 0.1s; }
.findname span:nth-child(3) { animation-delay: 0.2s; }
.findname span:nth-child(4) { animation-delay: 0.3s; }
.findname span:nth-child(5) { animation-delay: 0.4s; }
.findname span:nth-child(6) { animation-delay: 0.5s; }
.findname span:nth-child(7) { animation-delay: 0.6s; }
.findname span:nth-child(8) { animation-delay: 0.7s; }
.findname span:nth-child(9) { animation-delay: 0.8s; }

/* Add a slight backdrop for better readability */
.search-section::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  background: rgba(0, 0, 0, 0.4);
  z-index: 0;
}

.search-section * {
  position: relative;
  z-index: 1;
}

/* Improved Input Field */
.search-section input {
  width: 100%;
  max-width: 450px;
  padding: 12px 15px;
  border: none;
  border-radius: 8px;
  font-size: 18px;
  color: #333;
  background: rgba(255, 255, 255, 0.9);
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.2);
  transition: all 0.3s ease-in-out;
}

.search-section input:focus {
  outline: none;
  border: 2px solid orange;
  box-shadow: 0px 4px 15px rgba(255, 165, 0, 0.5);
}

/* Responsive Design */
@media (max-width: 768px) {
  .search-section {
    height: 400px;
    padding: 20px;
  }

  .search-section input {
    max-width: 90%;
    font-size: 16px;
    padding: 10px;
  }
}

.search-section button {
  margin-top: 10px;
  padding: 10px 20px;
  font-size: 16px;
  border: none;
  background: linear-gradient(to right, #dccb2b, #ff7e00);
  color: white;
  cursor: pointer;
  border-radius: 5px;
}

.search-section button:hover {
  background: linear-gradient(to right, #ff7e00, #dccb2b);
}

.services {
  background-image: linear-gradient(to right, rgb(174, 138, 138), rgb(68, 101, 55));
}

.service-box {
  display: inline-block;
  width: 280px;
  border-radius: 12px;
  overflow: hidden;
  box-shadow: 0px 6px 12px rgba(0, 0, 0, 0.15);
  text-align: center;
  font-family: "Poppins", sans-serif;
  margin: 20px;
  background: linear-gradient(to bottom, #ffffff, #f8f8f8);
  transition: transform 0.3s ease-in-out, box-shadow 0.3s ease-in-out;
  padding: 40px 28px;
}

.service-box:hover {
  transform: translateY(-5px);
  box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.25);
}

.service-box img {
  width: 100%;
  height: 200px;
  object-fit: cover;
  border-radius: 8px;
  transition: transform 0.3s ease-in-out;
}

.service-box:hover img {
  transform: scale(1.05);
}

.service-box h3 {
  margin: 15px 0;
  font-size: 22px;
  font-weight: 600;
  color: #333;
}

.service-box p {
  color: #555;
  padding: 0 15px;
  font-size: 15px;
  line-height: 1.6;
}

.service-box button {
  background: linear-gradient(to right, #ff7e00, #ff4500);
  color: white;
  border: none;
  padding: 12px;
  width: 100%;
  cursor: pointer;
  font-size: 16px;
  font-weight: 600;
  border-radius: 8px;
  transition: background 0.3s ease-in-out, transform 0.2s ease-in-out;
}

.service-box button:hover {
  background: linear-gradient(to right, #ff4500, #ff7e00);
  transform: scale(1.05);
}

.our-services {
  text-align: center;
  color: white;
  background: linear-gradient(to right, #000000, #333333);
  margin: 0px;
}

.aero {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  font-size: 22px;
  font-weight: bold;
  background: linear-gradient(to right, #ff7e00, #ff4500);
  color: white;
  padding: 8px 16px;
  border-radius: 5px;
  transition: all 0.3s ease-in-out;
}

.aero:hover {
  background: linear-gradient(to right, #ff4500, #ff7e00);
  transform: translateX(-50%) scale(1.1);
}

.slider-container {
  position: relative;
  width: 100%;
  display: flex;
  gap: 0;
  /* No gap between slides */
  margin: auto;
  overflow: hidden;
  /* Hide overflowed content */
  background-image: linear-gradient(to right, rgb(174, 138, 138), rgb(68, 101, 55));

}

/* Slider Wrapper */
.slider {
  display: flex;
  transition: transform 0.5s ease-in-out;
  width: calc(100% * var(--slides-count));
  /* Dynamically set width based on slides count */
}


/* Each Slide */
.slide {
  flex: 0 0 33.33%;
  /* Each slide takes 1/3 of the container */
  display: flex;
  justify-content: center;
  /* Center content horizontally */
  align-items: center;
  /* Center content vertically */
  width: 100%;
  /* Ensure each slide takes the full width of the flex item */
  height: 200px;
}

/* Box Style */
.box {
  display: flex;
  justify-content: center;
  /* Centers the content horizontally */
  align-items: flex-start;
  /* Aligns the content to the top */
  width: 100%;
  /* Adjust width as needed */
  max-width: 300px;
  height: 155px;
  /* Set a fixed height for the container */
  text-align: center;
  padding: 15px;
  background: #fff;
  border-radius: 10px;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.2);
}


.box:hover {
  transform: scale(1.05);
  /* Slight zoom effect on hover */
}

/* Image Styling */
.box img {
  width: 100%;  /* Covers the full width of the container */
  height: 100%; /* Adjusts height to fill the container */
  object-fit: cover; /* Ensures it fully covers the container */
  object-position: center 0px;
  border-radius: 15px;/* Slightly rounded corners */
}


/* Navigation Buttons */
.prev,
.next {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  background: rgba(0, 0, 0, 0.5);
  color: white;
  border: none;
  padding: 12px 16px;
  cursor: pointer;
  font-size: 24px;
  border-radius: 50%;
  transition: background 0.3s ease, transform 0.2s ease;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  
}

.prev {
  left: 10px;
}

.next {
  right: 10px;
}

.prev:hover,
.next:hover {
  background: rgba(0, 0, 0, 0.8);
  transform: translateY(-50%) scale(1.1);
}


.about-feedback {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  padding: 40px;
  gap: 30px;
  background-color: #f9f9f9;
}

.about-section,
.feedback-section {
  flex: 1;
  padding: 20px;
  background: white;
  border-radius: 10px;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.2);
}

.about-section h2,
.feedback-section h2 {
  color: #333;
  margin-bottom: 15px;
}

.feedback-section form {
  display: flex;
  flex-direction: column;
}

.feedback-section input,
.feedback-section textarea {
  width: 100%;
  padding: 10px;
  margin-bottom: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
}

.feedback-section button {
  background-color: orange;
  color: white;
  border: none;
  padding: 10px;
  cursor: pointer;
  font-size: 16px;
}

.feedback-section button:hover {
  background-color: darkorange;
}

@media (max-width: 768px) {
  .about-feedback {
    flex-direction: column;
  }
}

.about-feedback {
  background-image: linear-gradient(to right, rgb(174, 138, 138), rgb(68, 101, 55));
}

footer {
  background: linear-gradient(to right, #222, #111);
  color: white;
  padding: 50px 20px;
  font-family: "Poppins", sans-serif;
}

.footer-container {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  max-width: 1200px;
  margin: auto;
  gap: 30px;
}

.footer-column {
  flex: 1;
  min-width: 250px;
  margin: 10px;
}

.footer-column h3 {
  margin-bottom: 15px;
  font-size: 20px;
  font-weight: 600;
  border-bottom: 3px solid orange;
  display: inline-block;
  padding-bottom: 8px;
}

.footer-column ul {
  list-style: none;
  padding: 0;
}

.footer-column ul li {
  margin-bottom: 10px;
  transition: transform 0.3s ease-in-out;
}

.footer-column ul li:hover {
  transform: translateX(5px);
}

.footer-column ul li a {
  color: white;
  text-decoration: none;
  font-size: 16px;
  transition: color 0.3s ease-in-out;
}

.footer-column ul li a:hover {
  color: orange;
}

.footer-column p {
  font-size: 15px;
  line-height: 1.6;
  color: #bbb;
}

.social-icons {
  display: flex;
  gap: 15px;
  margin-top: 10px;
}

.social-icons a {
  display: inline-block;
  transition: transform 0.3s ease-in-out;
}

.social-icons a:hover {
  transform: scale(1.2);
}

.social-icons img {
  width: 28px;
  height: 28px;
  filter: brightness(0) invert(1);
  transition: transform 0.3s ease-in-out;
}

.social-icons img:hover {
  transform: scale(1.3);
  filter: brightness(1);
}

.footer-bottom {
  text-align: center;
  margin-top: 30px;
  padding-top: 15px;
  font-size: 14px;
  border-top: 1px solid rgba(255, 255, 255, 0.2);
  color: #aaa;
}

.footer-bottom a {
  color: orange;
  text-decoration: none;
  font-weight: 600;
  transition: color 0.3s;
}

.footer-bottom a:hover {
  color: #ff7700;
}

.social-icons {
  display: flex;
  gap: 15px;
}

.social-icon {
  font-size: 24px;
  color: white;
  text-decoration: none;
  transition: color 0.3s ease-in-out;
}

.social-icon:hover {
  color: blue;
  /* Change color on hover */
}
